<template>
    <div class="main">
        <el-table :data="tableData" v-if='tableData.length>0' border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
            <el-table-column prop="name" label="比对情况监控">
            </el-table-column>
            <el-table-column label="操作" fixed="right" width="220">
                <template slot-scope="scope">
                    <el-button @click="zxBtn(scope.row.item_key)" type="text" size="mini">{{scope.row.btn}}</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>

import {
    batchProcessingApi
} from '@/api/index';
export default {
    data() {
        return {
            isLoading: false,
            tableHeight: window.innerHeight - 80,
            tableData: [{
                name: '学生年级升级',
                item_key: "execute_upgrade",
                btn: '执行年级升级'
            },]
        };
    },
    mounted() {
    },
    methods: {
        zxBtn(e) {
            this.$confirm('是否执行比对程序?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            })
                .then(async () => {
                    this.isLoading = true;
                    const res = await this.axios.post(batchProcessingApi, { code: e });
                    if (res.data.code == 1) {
                        this.isLoading = false;
                        // this.$message({ showClose: true, message: '执行完毕', type: 'success', });
                        this.$alert('<strong>待处理数据总量：' + res.data.data + '</strong>', '处理结果', {
                            dangerouslyUseHTMLString: true
                        });
                    } else {
                        this.isLoading = false;
                        this.$message({ showClose: true, message: res.data.msg, type: 'error', });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作',
                    });
                });
        }
    },
};
</script>

<style lang="scss" scoped>
</style>



